<template>
  <div class="container py-5">
    <form class="w-100" @submit.prevent.stop="handleApplyUser()">
      <div class="text-center mb-4">
        <h1 class="h3 mb-3 font-weight-normal">建立你的帳號</h1>
      </div>

      <div class="form-label-group mb-2">
        <label for="name">帳號</label>
        <input
          id="account"
          v-model="account"
          name="account"
          type="text"
          class="form-control"
          required
          autofocus
        />
      </div>

      <div class="form-label-group mb-2">
        <label for="name">名稱</label>
        <input
          id="name"
          v-model="name"
          name="name"
          type="text"
          class="form-control"
          placeholder="name"
          autocomplete="username"
          required
          autofocus
        />
      </div>

      <div class="form-label-group mb-2">
        <label for="email">Email</label>
        <input
          id="email"
          v-model="email"
          name="email"
          type="email"
          class="form-control"
          placeholder="email"
          autocomplete="email"
          required
        />
      </div>

      <div class="form-label-group mb-3">
        <label for="password">密碼</label>
        <input
          id="password"
          v-model="password"
          name="password"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="new-password"
          required
        />
      </div>

      <div class="form-label-group mb-3">
        <label for="passwordConfirm">密碼確認</label>
        <input
          id="checkPassword"
          v-model="checkPassword"
          name="checkPassword"
          type="password"
          class="form-control"
          placeholder="checkPassword"
          autocomplete="checkPassword"
          required
        />
      </div>

      <button class="btn btn-lg btn-primary btn-block mb-3" type="submit">註冊</button>

      <div class="text-center mb-3">
        <p>
          <router-link to="/">取消</router-link>
        </p>
      </div>
    </form>
  </div>
</template>
<script>
import { Toast } from "./../utils/helpers";
import userAPI from "./../apis/users";
export default {
  data() {
    return {
      account: "",
      name: "",
      email: "",
      password: "",
      checkPassword: "",
    };
  },
  methods: {
    handleApplyUser() {
      if (this.password !== this.checkPassword) {
        Toast.fire({
          icon: "warning",
          title: "密碼輸入不一致",
        });
        return;
      } else if (
        !this.account ||
        !this.name ||
        !this.email ||
        !this.password ||
        !this.checkPassword
      ) {
        Toast.fire({
          icon: "warning",
          title: "欄位不能有空白",
        });
        return;
      }
      userAPI
        .applyUser({
          account: this.account,
          name: this.name,
          email: this.email,
          password: this.password,
          checkPassword: this.checkPassword,
        })
        .then((response) => {
          console.log("response", response);
          const data = response.data;
          //console.log(data);
          // 未成功
          if (data.status !== "success") {
            throw new Error(data.message);
          } else {
            // 成功
            this.$router.push("/login");
            Toast.fire({
              icon: "success",
              title: "註冊成功, 請登入",
            });
          }
        })
        .catch((error) => {
          // 顯示錯誤提示
          Toast.fire({
            icon: "warning",
            title: "註冊失敗",
          });
          console.log("error", error);
        });
    },
  },
};
</script>